import React, { Component } from 'react'
import { Modal, Button, Icon } from 'semantic-ui-react'
import { connect } from 'react-redux'
import { getMembers } from '../../store/actions/memberActions'
import { loadingDatas, loadedDatas } from '../../store/actions/loaderActions'
import axios from 'axios'

//    
class BanModal extends Component {
    
    state = {
        banModalOpen: false
    }

    //   ''  
    banMember = () => {
        const banTargetMemberId = this.props.memberId;

        this.props.loadingDatas();

        //   api 
        axios.put('/members/ban/' + banTargetMemberId, {
            memberId: banTargetMemberId
        })
        .then((res) => {
            console.log(res.data);

            this.props.getMembers();
        })
        .catch((err) => {
            
        })

        this.setState(() => {
            return {
                banModalOpen: false
            }
        })
    }

    //    
    openBanModal = () => {
        this.setState(() => {
            return {
                banModalOpen: true
            }
        })
    }

    //   ''  
    closeBanModal = () => {
        this.setState(() => {
            return {
                banModalOpen: false
            }
        })
    }

    render() {
        return (
            <div>
                <Button onClick={ this.openBanModal } size='small' color='red' labelPosition='left' icon><Icon name='ban'/></Button>
                <Modal 
                    open={ this.state.banModalOpen }
                    size='mini'
                >
                    <Modal.Header>  </Modal.Header>
                    <Modal.Content>
                        <p>    ?</p>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button onClick={ this.banMember } positive></Button>
                        <Button onClick={ this.closeBanModal } negative></Button>
                    </Modal.Actions>
                </Modal>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        loadingDatas: () => dispatch(loadingDatas()),
        loadedDatas: () => dispatch(loadedDatas()),
        getMembers: () => dispatch(getMembers())
    }
}

export default connect(null, mapDispatchToProps)(BanModal);