import React from 'react'
import { Icon, Header } from 'semantic-ui-react'

const style = {
    header: {
        marginBottom: '2em'
    }
}

//   
const PageHeader = () => (
    <Header as='h1' style={ style.header } dividing>
        <Icon name='users' />
        <Header.Content>
             
        <Header.Subheader>            .</Header.Subheader>
        </Header.Content>
    </Header>
);

export default PageHeader;